* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 60%;
	height: 100vh;
	border: 1px solid rgb(0, 0, 0, 0.5);
  	text-align: center;
	font-family: 'Lato', sans-serif;
	margin: 0 auto;
}

.app header {
	position: absolute;
	top: 0;
	padding-top: 3%;
	font-size: 20px;
	font-weight: 500;
	color:rgb(95, 99, 104);
}

.app footer{
	position: absolute;
	bottom: 0;
	color:rgb(95, 99, 104);
	font-size: 12px;
}
.app footer a{
	text-decoration: none;
	color: #2283F8;
}

.displayWrapper{
	position: absolute;
	top: 35%;
	font-size: 60px;
	width: 100%;
	font-weight: 700;
}

.withMarks{
	position: absolute;
	padding-left: 8px;
	top: 25%;
	text-align: left;
	font-size: 42px;
	font-weight: 500;
}

.displayWrapper .textMark{
	position: relative;
	top: 6px;
	font-size: 16px;
	color:rgb(95, 99, 104);
}

.markWrapper{
	padding: 0 6px;
	position: absolute;
	top: 42%;
	width: 100%;
	height: 36%;
	overflow-y: scroll;
}

.markWrapper::-webkit-scrollbar {
	width: 8px;
	background-color: #f9f9fd;
}
  
.markWrapper::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-color:rgb(95, 99, 104, 0.2);
}
  
.markWrapper::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 0px rgba(0, 0, 0, 0);
	border-radius: 10px;
	background-color: transparent;
}

.markWrapper .markFlex {
  	display: flex;
	justify-content: space-between;
	margin-bottom: 5px;
	letter-spacing: 1px;
}

.markWrapper .markFlex .numOfMark, .different {
	color:rgb(95, 99, 104);
}

#marks{
	display: inline-block;
	position: relative;
	top: 0px;
	width: 16px;
	height: 16px;
	color:rgb(95, 99, 104);
}
.numOfMark{
	position: relative;
	bottom: 7px;
	left: 7px;
}


.btnWrapper{
	position: absolute;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	width: 200px;
	top: 80%;
}

.btnWrapper span #btnStart, #btnMark, 
#btnStop, #btnReset, #btnResume{
	border: 1px solid rgba(0, 0, 0, 0.1);
	border-radius: 50%;
	font-size: 48px;
	color: #2283F8;
	box-shadow: 1px 1px 10px 4px  rgb(94, 99, 104, 0.16);
}




